header .Middle-header .our-business {
      
  font-size: 1.2em;
  float: left;
  margin-left: -6em;
  margin-top: -3em;
  cursor: pointer;

  letter-spacing: 1px;
}


header .Mid-header1 .business button:hover , header .Mid-header2 .mission button:hover{
    color: #baf7bb;
  -webkit-mask-image: -webkit-linear-gradient(165deg, rgba(0,0,0,0.7) 30%, #000 50%, rgba(0,0,0,0.7) 70%);
  -webkit-mask-size: 200%;
  -webkit-animation: shine 2s infinite;
          animation: shine 2s infinite;
}
@-webkit-keyframes shine {
  from {
    -webkit-mask-position: 150%;
  }
  
  to {
    -webkit-mask-position: -50%;
  }
}
header .Middle-header .contact-us {
  
  font-size: 1.2em;
  float: right;
  margin-top: -3em;
  margin-right: -6em;
  cursor: pointer;

  
  letter-spacing: 1px;
  -webkit-animation-name: middleHeader;
          animation-name: middleHeader;
   -webkit-animation-duration: 5s;
           animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;  

}
  @-webkit-keyframes middleHeader {
  

   
  }
  @keyframes middleHeader {
  

   
  }
header .Low-header {
  
  margin-top: -1.1em;
}
header .Low-header p {
  
