// Resetting default styles
.carousel__slides {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

// Fix for jumping arrows
div.carousel-container {
  display: -webkit-flex!important;
  display: flex!important;

  -webkit-flex-direction: column;

          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 210px;




}
.carousel-container p {
    color: #fff;
        width: 90%;
    margin: 0 auto;
    font-family: 'Roboto Slab', sans-serif;
}
.carousel {
  position: relative;
   -webkit-transition: all 3s ease-in-out;
   transition: all 3s ease-in-out;
       padding: 0.5em;
   
    border: 1px solid #fff;

        width: 70%;
        margin: 0 auto;
}

// Carousel slides
li.carousel__slide {
  margin-right: auto;
  margin-left: auto;

  max-width: 900px;
  list-style-type: none;
  text-align: center;
}
  @media (max-width: 991px) {
   li.carousel__slide {
    padding-right: 60px;
    padding-left: 60px;
  }
}
li.carousel__slide {
    display: none;
}
  li.carousel__slide--active {
    display: block; 
  }


// Content of slides
li.carousel-slide__content {
  margin-bottom: 19px;
 

  font-size: 16px;
  
}



  @media (max-width: 992px - 1px) {
    li.carousel-slide__content {
    font-size: 18px;
  }
}

.carousel-slide__author,
.carousel-slide__source {
  font-family: 'Roboto', arial, sans-serif;
  font-size: 14px;
}
  @media (min-width: 992px) {
    .carousel-slide__author,
.carousel-slide__source {
    font-size: 16px;
  }
}

.carousel-slide__source {
  font-style: italic;
  color: #fff;
}

// Carousel arrows
 a.carousel__arrow.carousel__arrow--right,  a.carousel__arrow.carousel__arrow--left {
  position: absolute;
  top: 50%;
  display: block;
  color: #000;
  cursor: pointer;
  opacity: .75;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: opacity .15s cubic-bezier(.4, 0, 1, 1);
  transition: opacity .15s cubic-bezier(.4, 0, 1, 1);
}
  .carousel__arrow:focus {
    outline: 0;
  }

 span.fa.fa-2x:hover {
      color: #baf7bb;
 }

 a.carousel__arrow.carousel__arrow--left {
    left: 2em;
    position: absolute;
   top: 0;
   color: #fff;
  }

 a.carousel__arrow.carousel__arrow--right {
    right: 2em;
   position: absolute;
   top: 0;
   color: #fff;
   
  }


// Carousel indicators
.carousel__indicators {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 20px;
}
  .carousel__indicators li:not(:last-child) {
  
      margin-right: 9px;
    }

.carousel__indicators li {
      display: inline-block;
}
a.carousel__indicator {
  display: inline-block;

  width: 24px;
  height: 3px;
  background-color: #fff;
  cursor: pointer;
  opacity: .3;
  
}
  .carousel__indicator:hover {
    opacity: .67;
  }

   a.carousel__indicator--active, a.carousel__indicator--active a.carousel__indicator, a.carousel__indicator--active:hover {
 
      opacity: .75;
    }

